<template>
  <div class="demo-page three-js-demo">
    <el-row>
      <el-col :span="12">
        <div ref="container" class="three-js-demo-container">

        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import DemoPageMixin from '@/mixins/demo-page-mixin'
import * as THREE from 'three'
console.log('THREE', THREE)
export default {
  name: 'ThreeJsDemo001',
  mixins: [DemoPageMixin],
  data () {
    return {
      threeProps: {
        camera: null,
        scene: null,
        renderer: null,
        geometry: null,
        material: null,
        mesh: null,
        animate: this.animate
      }
    }
  },
  mounted () {
    this.threeProps.animate = () => {
      requestAnimationFrame(this.threeProps.animate)
      this.mesh.rotation.x += 0.01
      this.mesh.rotation.y += 0.02
      this.renderer.render(this.scene, this.camera)
    }
    this.init()
    this.threeProps.animate()
  },
  methods: {
    init () {
      const containerWidth = 300
      const containerHeight = 300
      this.camera = new THREE.PerspectiveCamera(70, containerWidth / containerHeight, 0.01, 10)
      this.camera.position.z = 1

      this.scene = new THREE.Scene()
      this.geometry = new THREE.BoxGeometry(0.2, 0.2, 0.2)
      this.material = new THREE.MeshNormalMaterial()

      this.mesh = new THREE.Mesh(this.geometry, this.material)
      this.scene.add(this.mesh)

      this.renderer = new THREE.WebGLRenderer({
        antialias: true
      })
      this.renderer.setSize(containerWidth, containerHeight)
      this.$refs['container'].appendChild(this.renderer.domElement)
    }
  }
}
</script>

